/* -----------------------------------   Navbar    ------------------------------------ */

.nav {
    background-color: #d1be1cf7;  /* !! */
    font-weight: bold;
    font-style: italic;
    color: rgb(56, 36, 172);
    display: flex;
    justify-content: center;  /* !! */
    align-items: center;
    gap: 2rem;
    /* padding: 0 1rem;  !!  */
    position: sticky;
    top: 0;
    z-index: 100;
}

.homeLogoLink {
    border-radius: 2px;
    width: 50px;
}
  
.siteLink {
    font-size: 2rem;

}
  
.nav a {
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
}
  
.nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
}

.nav li {
    border-radius: 2px;
    padding: 5px;
    text-align: center;
    width: 25%; /* !! */
}
  
.nav li.active {
    background-color: #777;
}
  
.nav li:hover {
    background-color: #5818c0c2;  /* !! */
    color: yellow;
}

@media (min-width: 360px) {
    .nav {
        gap: 0.5rem;
    }

    .nav ul {
        gap: unset;
    }
}

@media (max-width: 380px) {
    .nav {
        font-size: 10px;
        width: 100%;
        gap: unset;
    }
    .nav a {
        width: 21%;
    }
    .homeLogoLink {
        width: 94%;
    }
    .nav ul {
        gap: unset;
    }
}

@media (max-width: 300px) {
    .nav {
        font-size: 10px;
    }
}

@media (min-width: 500px) {
    .nav {
        font-size: 15px;
    }
}

@media (min-width: 767px) {  
    .nav {
      display: none;
    }
}
  

  